.loader {
	@include size(100%,100vh);
	position: absolute;
	left: 0;
	top: 0;
	z-index: 49;

	&__clouds {
		position: absolute;
		left: 0;
		top: 0;
		z-index: 1;
		width: 100%;
		height: 100%; }

	&__cloud {
		width: 100%;
		height: 100%;
		background-repeat: no-repeat;
		position: absolute;
		top: 0;
		left: 0;

		&--tr {
			background-image: url("../images/clouds/top_right.png");
			background-position: 100% 20%;
			z-index: 1; }

		&--br {
			background-image: url("../images/clouds/bottom_right.png");
			background-position: 100% 20%;
			z-index: 2; }

		&--oc {
			background-image: url("../images/clouds/over_center.png");
			background-position: 50%;
			z-index: 3; }

		&--cl {
			background-image: url("../images/clouds/center_left.png");
			background-position: 0 0;
			z-index: 4; }

		&--white {
			background-color: $white;
			z-index: 5; }

		@include bp($point-7) {
			background-size: contain; } }

	&__spinner {
		position: absolute;
		left: 50%;
		top: 55%;
		transform-origin: 0 0;
		transform: translate(-50%,-50%);
		animation: spinner 1.5s infinite linear;
		will-change: transform;
		z-index: 2;

		@media all and (max-height: 750px) {
			top: 60%; } }

	&__number {
		position: absolute;
		left: 50%;
		top: 55%;
		transform: translate(-50%,-50%);
		color: #4F5E72;
		font-family: $baseScndFont;
		font-size: 15px;
		line-height: 305%;
		text-align: center;
		text-transform: uppercase;
		z-index: 2;

		@media all and (max-height: 750px) {
			top: 60%; } } }

@keyframes spinner {
	0% {
		transform: rotate(0) translate(-50%,-50%); }
	100% {
		transform: rotate(360deg) translate(-50%,-50%); } }

.gold_award {
	position: fixed;
	top: 50%;
	transform: translateY(-50%);
	right: 0;
	z-index: 10;
	cursor: pointer;
	img {
		max-height: 305px; }

	@include bp(1200px) {
