.popup {
	position: fixed;
	left: 0;
	top: 0;
	@include size(100%, 100vh);
	display: flex;
	flex-wrap: wrap;
	background-color: $menuBg;
	z-index: 500;
	transform: translateY(-100%);
	transition: all .5s ease-in-out;
	overflow: hidden;
	user-select: none;

	&--small {
		background: transparent;
		.popup__action {
			height: auto;
			flex: 0 0 80px;
			padding: 0; }

		.action__icon {
			display: none; }

		.action__line {
			border-color: #C4C4C4;
			&:nth-child(2) {
				height: 40%; } }

		svg {
			line {
				stroke: #4F5E72; } } }

	&__wrapper {
		display: flex;
		align-self: center;
		margin: 0 auto;
		position: relative;
		z-index: 2;
		max-width: 1000px;
		width: 100%;
		background-color: #fff;
		max-height: 100vh;
		overflow-y: auto; }

	&__overlay {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background: rgba(0, 0, 0, 0.50);
		z-index: 1; }

	&__action {
		flex: 0 0 154px;

		.action__line {
			opacity: 0.3 !important; } }

	&__container {
		flex: 0 0 calc(100% - 155px); }

	&__nav {
		position: absolute;
		background-color: $white;
		bottom: 0;
		right: 0;
		@include size(50%, auto);
		border-top: 1px solid #F2F2F2;
		display: flex;

		@include bp($point-3-low) {
			right: unset;
			left: 0;
			@include size(100vw, auto); }

		&-item {
			cursor: pointer;
			padding: 41px 0;
			text-align: center;
			flex: 0 0 50%;
			font-size: 14px;
			line-height: 130%;
			letter-spacing: 0.02em;
			text-transform: uppercase;
			font-feature-settings: 'salt' on;
			color: #C4C4C4;
			transition: all .3s ease-in-out;

			@include bp($point-3-low) {
				font-size: 12px;
				padding: 23px 0; }

			&--active {
				color: #005FAB; }

			&--prev {
				border-right: 1px solid #F2F2F2; } } }


	&.js-popup-opened {
		transform: translateY(0); } }

#video,
#cocktail,
#bottle,
#products,
#blend {
	.js-follower {
		background-color: $white;
		border-radius: 50%;
		line {
			stroke: #4F5E72; } } }

#video,
#cocktail,
#bottle,
#products,
#blend {
	.action {
		&__icon {
			display: none; }

		&__close {
			position: fixed;
			top: 20px;
			right: 20px; }

		&__line {
			display: none; } }

	.popup {
		&__action {
			flex: unset;
			padding: 0; } } }

#blend,
#video {
	.popup {
		&__container {
			flex: 0 0 100%; } } }


#bottle {
	background: url("../images/bg_popup.jpg") center no-repeat;
	background-size: cover;

	.popup__action {
		position: absolute;
		left: 0;
		top: 0;
		height: 100%;

		@include bp($point-3-low) {
			height: auto; } }

	.popup__container {
		flex: 0 0 100%; }
	.action__line {
		border-color: $white; } }

#cocktail {
	background-color: $white;

	.content {
		position: absolute;
		left: 0;
		top: 0; }

	.js-link {
		cursor: default; }
	.popup__action {
		position: absolute;
		left: 0;
		top: 0;
		height: 100%;

		@include bp($point-3-low) {
			height: auto; } }

	.popup__container {
		flex: 0 0 100%; }
	.action__line {
		border-color: $white; } }

.content {
	display: flex;
	@include size(100%);

	@include bp($point-3-low) {
		flex-direction: column; }

	&__video {
		width: 100vw;
		height: auto;

		&-bar {
			cursor: pointer;
			position: absolute;
			left: 0;
			bottom: 44px;
			right: 0;
			margin: auto;
			width: 80%;
			height: 3px;
			background-color: rgba(255, 255, 255, 0.2);
			overflow: hidden; }

		&-progress {
			display: block;
			width: 100%;
			height: 100%;
			background-color: $white;
			transform: scaleX(0);
			transform-origin: 0 0;
			position: relative;
			z-index: 1; }

		&-drag {
			width: 1px;
			height: 20px;
			top: -10px;
			position: absolute;
			z-index: 2;
			transform-origin: 0 0; }

		&-bg {
			position: absolute;
			left: 0;
			top: 0;
			@include size(100%);
			background-size: cover;
			background-position: center;
			background-repeat: no-repeat; } }

	&__icon {
		position: absolute;
		left: 0;
		right: 0;
		top: 0;
		bottom: 0;
		margin: auto;
		cursor: pointer;
		z-index: 10; }

	&__item {
		@include size(50%, 100%);
		background-color: #fff;

		iframe {
			height: 100%;
			width: 100%; }

		@include bp($point-3-low) {
			width: 100%; }

		&--left {
			background-size: cover;
			background-position: center;
			background-repeat: no-repeat;
			position: relative;
			.content__video {
				background-color: $black;
				width: 100%;
				height: 100%; } }

		&--right {
			height: 100vh;
			overflow-y: auto;
			padding: 96px 175px 88px 150px;

			@include bp($point-6) {
				padding: 50px; }

			@include bp($point-3-low) {
				padding: 30px 25px 30px 20px; } } }

	&__bottle {

		&-icon {
			@include size(50px);
			display: none;

			&--left {
				margin-left: auto; }

			&--right {
				margin-right: auto; } }

		&-img {
			z-index: 1;
			position: absolute;
			left: 0;
			top: 0;
			right: 0;
			bottom: 0;
			margin: auto;
			height: 80vh;
			width: 100%;
			display: flex;
			justify-content: center;

			img {
				height: 80vh;
				max-width: 184px;
				position: absolute;
				transition: opacity 0.5s ease-in-out;
				pointer-events: none; } }

		&-descr {
			display: flex;
			flex-direction: column;
			z-index: 2;
			position: absolute;
			left: 50%;
			top: 50%;
			transform: translate(-50%,-50%);

			&--left {
				text-align: right;
				left: 25.7%;
				top: 20.5%;

				@include bp($point-6) {
					top: 30%;
					max-width: 250px; }

				.content__bottle-title {
					text-align: right;
					margin: 0 0 21px auto; } }

			&--left-two {
				display: none;
				left: 26.7%;
				top: 49.5%;

				.content__bottle-title {
					max-width: 373px; }

				.content__bottle-line--left {
					width: 4vw;
					right: -110px;

					@include bp(1024px) {
						right: -51px; } } }

			&--right {
				left: 76.3%;
				top: 57.1%;


				.content__bottle-title {
					margin: 0 0 35px 0; } }

			&--right-two {
				display: none;
				top: 76.1%; } }

		&-title {
			margin: 0 0 21px 0;
			max-width: 300px;
			font-weight: normal;
			font-size: 36px;
			line-height: 110%;
			letter-spacing: 0.1em;
			text-transform: uppercase;
			font-feature-settings: 'salt' on;

			@include bp(1024px) {
				font-size: 22px; } }

		&-text {
			max-width: 387px;
			font-family: $baseScndFont;
			font-size: 18px;
			line-height: 160%;
			letter-spacing: 0.02em;
			font-feature-settings: 'salt' on;

			@include bp(1024px) {
				font-size: 14px; }

			&--strong {
				font-weight: 600; }

			&:nth-child(5) {
				margin: 35px 0 0; } }

		&-line {
			position: absolute;
			right: 0;
			top: 0;
			@include size(8.2vw,1px);
			border: 1px solid $white;

			&--left {
				top: 35px;
				right: -227px;
				@include size(10vw,1px);

				@include bp(1880px) {
					right: -6vw;
					width: 5vw; } }

			&--right {
				left: -197px;
				top: 20px;

				@include bp(1880px) {
					left: -6vw;
					width: 5vw; } } } }
	&__grapic {
		display: flex;
		flex-direction: column;
		align-items: center;
		position: absolute;
		top: 40px;
		left: 20px;

		&-text {
			margin-top: 25px;
			font-size: 14px;
			line-height: 130%;
			text-align: center;
			letter-spacing: 0.02em;
			font-feature-settings: 'salt' on;
			opacity: 0.5;
			max-width: 144px; }

		svg {
			@include size(63px,41px); } } }

.cocktail {
